import React from 'react'
import ProductDetailsSection from '../../components/ProductDetailsSection'
import pierres from '../../img/produits/pierres.jpg'
import bracelets from '../../img/produits/bracelets.jpg'
import pendentifs from '../../img/produits/pendentifs.jpg'

export default function Lithotherapie() {
   return (
        <div class="product_details">
            <h1>Lithothrapie</h1>
            <p className="product_details_intro">
                La <strong>Lithothrapie</strong> est le fait d'utiliser les <strong>pierres</strong> pour travailler sur soi mme. Elles peuvent vous donner un coup de pouce dans de nombreux domaines : rduire le stress, avoir confiance en soi, trouver les mots juste.
                <br/>Mais ultimement, elles ne font que vous reconnecter avec un potentiel qui existe dj en vous.
            </p>
            <ProductDetailsSection 
            title="Pierres en forme libre" 
            paragraph="Profitez d'une trs grande slection de pierres, qu'elles soient brut ou roule, petites  garder sur soi, ou plus grosse  poser dans la pice."
            img={pierres}
            />

            <ProductDetailsSection 
            title="Bracelets" 
            paragraph="Si vous voulez emporter votre solution bien-tre avec vous, nous pouvons vous proposer des bracelets de perles, que vous cherchiez une pierre en particulier, une de nos combinaisons maison ou mme un bracelet personnalis."
            img={bracelets}
            reversed={true}
            />

            <ProductDetailsSection 
            title="Pendentifs" 
            paragraph="Dcouvrez galement nos pendentifs en pierres, pour ceux qui cherchent une qualit suprieure, ou simplement une autre faon de transporter sa pierre."
            img={pendentifs}
            />
        </div>
   )
}