 import React from 'react'
 import pendule from '../img/evenements/pendule.webp'
 import spelljar from '../img/evenements/spelljar.jpg'

 function Evenements() {
   return (
     <div className="page_evenement">
       <h1>
         Evnements
       </h1>

       <p className="evenement_intro"> 
        Lune de Crystal vous propose des lments rguliers, comme des ateliers, ou des vnements exceptionnels, comme des collaborations avec d'autre thrapeutes, mais aussi des ateliers sur demande.
       </p>

        <section className="section_evenement">
          <div class="evenement_img_container">
            <img src={pendule} alt=""></img>
          </div>
          <h2>Ateliers Pendule</h2>
          <span>20 1 heure</span>
          <p>Utiliser le <strong>pendule</strong> pour amliorer son quotidien</p>
          <p>Rejoignez une de nos sances rgulires (4 personnes) : 
            <br/> 13 Septembre  14h00
            <br/> 11 Octobre  14h00 
            <br/> 15 Novembre  14h00</p>
          <p>Si vous avez dj un groupe de 2  4 personnes, <br/>contactez nous pour organiser un atelier  une autre date, rien que pour vous !</p>
        </section>

        <section className="section_evenement">
          <div class="evenement_img_container">
            <img src={spelljar} alt=""></img>
          </div>
          <h2>Ateliers Fioles magiques</h2>
          <span>20 1 heure</span>
          <p>Repartez avec deux fioles et beaucoup de conseils</p>
          <p>Rejoignez une de nos sances rgulires (4 personnes) : 
            <br/> 20 Septembre  14h00
            <br/> 18 Octobre  14h00 
            <br/> 22 Novembre  14h00</p>
          <p>Si vous avez dj un groupe de 2  4 personnes, <br/>contactez nous pour organiser un atelier  une autre date, rien que pour vous !</p>
        </section>
        
     </div>
     
   )
 }

 export default Evenements;