 import React from 'react'
 import holistique from '../img/prestations/holistique.jpg'
 import vibratoire from '../img/prestations/vibratoire.jpg'
 import lumiere from '../img/prestations/lumiere.png'
 
 function Prestations() {
   return (
     <div className="page_prestations">

       <h1>
         Prestations
       </h1>
       <p className="prestation_intro">
         L'activit conseil et vente est complte par des sances <strong>bien-tre</strong> sur rendez vous. Profitez de moments d'harmonisation nergtique dans un cocon de <strong>bien-tre</strong> dans l'arrire boutique.
       </p>

        <section className="section_prestations">
          <div class="prestation_img_container">
            <img src={holistique} alt=""></img>
          </div>
          <h2>Sance "Harmonisation Holistique"</h2>
          <span>45 1 heure</span>
          <p>
            Nettoyage profond des diffrents corps nergtiques, grce aux <strong>cristaux</strong> et aux <strong>tambours chamanique</strong>.
            <br/>Librations des chocs et blessures du pass, des nfluences ngatives et des blocages.
          </p>
        </section>

        <section className="section_prestations">
          <div class="prestation_img_container">
            <img src={vibratoire} alt=""></img>
          </div>
          <h2>Sance vibratoire</h2>
          <span>25 30 minutes</span>
          <p>
            Bols tibtains, bols enc ristal et carillons koshi.
            <br/>Libre des tensions physiques et mentales.
            <br/>Soulagement motionnel.
          </p>
        </section>

        <section className="section_prestations">
          <div class="prestation_img_container">
            <img src={lumiere} alt=""></img>
          </div>
          <h2>Sance "Lumire de cristal"</h2>
          <span>25 30 minutes</span>
          <p>
            Synergie des <strong>cristaux</strong>, de la lumire, des couleurs.
            <br/>Accueillez une nergie pure et puissante, de nettoyage et de dveloppement personnel et spirituel.
          </p>
        </section>

     </div>
   )
 }

 export default Prestations;