import React from 'react'
import ProductDetailsSection from '../../components/ProductDetailsSection'
import tibetain from '../../img/produits/tibetain.jpg'
import cristal from '../../img/produits/cristal.jpg'
import tambour from '../../img/produits/tambour.jpg'

export default function Sonotherapie() {
   return (
        <div class="product_details">
            <h1>Sonothrapie</h1>
            <p className="product_details_intro">
                La <strong>Sonothrapie</strong> consiste  utiliser des instruments comme des bols ou des tambours pour envoyer des vibrations <strong>positives</strong>.
            </p>
            <ProductDetailsSection 
            title="Bols tibtain" 
            paragraph="Venez tester des bols tibtains traditionnels produits  travers un mlange de 7 mtaux. Chaque bol  des fonctions et des caractristiques qui lui sont propre."
            img={tibetain}
            />

            <ProductDetailsSection 
            title="Bols en cristaux" 
            paragraph="Dcouvrez le son pur d'un bol en cristal, avec ses frquences diffrentes et complmentaire du bol tibtain."
            img={cristal}
            reversed={true}
            />

            <ProductDetailsSection 
            title="Autre" 
            paragraph="Nous proposons galement des tambours chamanique et des carillons koshi."
            img={tambour}
            />
        </div>
   )
}