import React from 'react'
import ProductDetailsSection from '../../components/ProductDetailsSection'
import breligion from '../../img/produits/breligion.jpg'
import medailles from '../../img/produits/medailles.jpg'
import statuettes from '../../img/produits/statuettes.jpg'

export default function Religion() {
   return (
        <div class="product_details">
            <h1>Religion</h1>
            <p className="product_details_intro">
                Nous possdons des articles religieux, comme des <strong>bougies</strong> (<strong>neuvaines</strong> et votives), des mdailles ou des statuettes, pour rpondre  vos besoins <strong>spirituels</strong>.
            </p>
            <ProductDetailsSection 
            title="Bougies religieuses" 
            paragraph="Nous possdons des neuvaines  l'image de nombreux saints et anges, pour vos prires."
            img={breligion}
            />

            <ProductDetailsSection 
            title="Mdailles" 
            paragraph="Choisissez parmi un vaste slection des mdailles de saints pour rpondre  vos besoins."
            img={medailles}
            reversed={true}
            />

            <ProductDetailsSection 
            title="Statuettes" 
            paragraph="Dcorez votre maison avec des statuettes issues de diverses religions pour vous apporter la chance et le bonheur."
            img={statuettes}
            />
        </div>
   )
}