import React from 'react'
import ProductDetailsSection from '../../components/ProductDetailsSection'
import encens from '../../img/produits/Encens.jpg';
import sauge from '../../img/produits/Sauge.jpg'
import resine from '../../img/produits/Resines.jpg'

export default function Encens() {
   return (
        <div class="product_details">
            <h1>Encens et fumigations</h1>
            <p className="product_details_intro">
                Pour amliorer ou purifier les nergies d'un lieu, vous pouvez utiliser des <strong>encens</strong> traditionnels, des plantes, bois ou des <strong>rsines</strong>  brler.
            </p>
            <ProductDetailsSection 
            title="Encens" 
            paragraph="Retrouvez un grand choix d'encens traditionnels de qualit, parmi des marques suivies ; Satya, Green Tree, HEM, Herbio, Sagrada Madre..."
            img={encens}
            />

            <ProductDetailsSection 
            title="Sauge et Palo Santo" 
            paragraph="La sauge et le palo santo sont un classique pour la purification. Vous trouverez de la sauge en fagot ou en sachet."
            img={sauge}
            reversed={true}
            />

            <ProductDetailsSection 
            title="Rsines et mlanges" 
            paragraph="Les rsines  brler sur charbon sont trs puissantes au niveau nergtique. Vous trouverez les classiques (benjoin, myrrhe, oliban...) ainsi que les religieux (pontifical, Marie qui dfait les noeuds...) et des mlanges."
            img={resine}
            />
        </div>
   )
}